
<template>
  <div class="igg" @click="itemClick">
    <div>potal</div>
  </div>
</template>

<script>
import { geoips } from "../../../utils/geoip";
export default {
  name: "igg",
  data() {
    return {
      servers: [],
      loading: null,
      tpl: "",
    };
  },
  created() {
    console.log("geoips", geoips);
  },
  methods: {
    async itemClick() {
      this.loading = weui.loading();
      let servers = await this.$store.dispatch("chromeextV2Getserver");
      console.log({ servers });
      this.loading.hide();
      this.servers = servers;
      weui.actionSheet(
        servers.map((item) => {
          return {
            label: item.name,
            onClick: () => {
              this.chromeextPacShow(item);
            },
          };
        })
      );
    },
    async chromeextPacShow(item) {
      let tpl = await this.$store.dispatch("chromeextPacShow", { sid: item.line_sn, gpd: 1 });
      let geoip_data = JSON.stringify(geoips)
      console.log(item.name, tpl.split('__GEOIP_LIST__;')[1].split('var direct=')[0] );
      tpl = tpl.replace('__GEOIP_LIST__', geoip_data)
      this.tpl = tpl
      // console.log({ tpl });
    },
  },
};
</script>

