<template>
  <div class="minVideo3" :style="`min-height: ${minHeight};`">
    <div class="swiper">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in rows" :key="index">
          <div v-if="mySwiper && Math.abs(mySwiper.activeIndex - index) <= 1" style="height:100%">
            <video
              v-if="Math.abs(mySwiper.activeIndex - index) <= 1"
              class="video"
              :id="item.vodrow.vodid"
              controls="controls"
              ref="video"
              playsinline
              :autoplay="mySwiper.activeIndex == index"
              width="100%"
              :src="item.videoUrl"
              :poster="item.vodrow && item.vodrow.coverpic"
            ></video>
            <!-- <img
              v-if="index == mySwiper.activeIndex"
              @load="loadImage"
              class="img"
              width="100%"
              :src="item.vodrow.coverpic"
            /> -->
          </div>
        </div>
      </div>
      <!-- 如果需要分页器 -->
      <!-- <div class="swiper-pagination"></div> -->

      <!-- 如果需要导航按钮 -->
      <!-- <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div> -->

      <!-- 如果需要滚动条 -->
      <!-- <div class="swiper-scrollbar"></div> -->
    </div>
    <div
      style="
        margin-left: 20px;
        position: absolute;
        bottom: 20px;
        width: calc(100% - 40px);
        z-index: 100;
      "
    >
      <van-slider
        v-model="sliderValue"
        @change="onChange"
        :max="rows.length - 1"
        inactive-color="e0e0e0"
      >
        <template #button>
          <div class="custom-button" style="">{{ sliderValue }}</div>
        </template>
      </van-slider>
    </div>
  </div>
</template>

<script>
import config from "../js/config.js";
export default {
  data() {
    return {
      sliderValue: 0,
      minHeight: "100vh",
      mySwiper: null,
      rows: (() => {
        return Array.from(Array(0)).map((item, index) => {
          return { url: require(`@/assets/test/${index + 1}.jpg`) };
        });
      })(),
    };
  },
  computed: {
    currentItem() {
      return this.rows.length ? this.rows[this.mySwiper.activeIndex ] : null;
    },
  },
  watch: {
    "mySwiper.activeIndex": {
      //   immediate: true,
      //   deep: true,
      handler: function (activeIndex) {
        this.sliderValue = activeIndex;
        console.log("mySwiper", activeIndex);
        this.videoUrlUpdate();
        if (activeIndex >= this.rows.length - 2) {
          this.loadData(false);
        }
      },
    },
  },
  created() {
    this.minHeight = window.innerHeight + "px";
    this.loadData(true);
  },
  mounted() {
    this.initSwiper();
  },
  methods: {
    loadImage(){

    },
    onChange(e) {
      this.mySwiper.slideTo(e);
    },
    loadData(isFirst) {
      return this.$axios
        .request({
          url: config.origin + "/minivod/reqlist",
          params: config.query(),
        })
        .then((res) => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            var rows = [];
            res.data.data.rows.map((item) => {
              if (item.vodrow && item.vodrow.vodid) {
                item.videoUrl = "";
                rows.push(item);
              }
            });
            if (isFirst) {
              this.rows = rows;
            } else {
              this.rows.push(...rows);
            }
            this.$nextTick(() => {
              this.mySwiper.updateSlides();
            });
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch((res) => {
          this.isMoreLoading = false;
          weui.topTips(res.message || "fail");
        });
    },
    videoUrlUpdate() {
      //   document.title = this.rows[this.mySwiper.activeIndex].vodrow.title;
      this.$nextTick(() => {
        this.$refs.video &&
          this.$refs.video.forEach((video) => {
            if (video.id == this.currentItem.vodrow.vodid) {
              video.play();
            } else {
              video.pause();
            }
          });
      });
      this.rows
        .filter((item, i) => Math.abs(this.mySwiper.activeIndex - i) <= 1)
        .forEach((item) => {
          if (!item.videoUrl) {
            this.$axios
              .request({
                url: config.origin + item.vodrow.play_url,
                params: config.query(),
              })
              .then((res) => {
                item.videoUrl =
                  res.data.data.httpurl || res.data.data.httpurl_preview;
              });
          }
        });
    },
    initSwiper() {
      var mySwiper = new Swiper(".swiper", {
        // direction: "vertical", // 垂直切换选项
        loop: false, // 循环模式选项
        // autoHeight: true,

        // 如果需要分页器
        pagination: {
          el: ".swiper-pagination",
        },

        // 如果需要前进后退按钮
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },

        // 如果需要滚动条
        scrollbar: {
          el: ".swiper-scrollbar",
        },
      });
      this.mySwiper = Array.isArray(mySwiper)
        ? mySwiper[mySwiper.length - 1]
        : mySwiper;
    },
  },
};
</script>

<style lang="less" scoped>
.minVideo3 {
  .swiper {
    width: 100%;
    // height: 300px;
    .img {
    }
  }
  .custom-button {
    padding: 2px 10px;
    background-color: #1989fa;
    color: white;
    border-radius: 10px;
  }
}
</style>
